.header {
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background-color: #000;

		z-index: 3;
	}
	position: fixed;
	top: 0;
	left: 0;

	width: 100%;

	z-index: 3;

	color: #000;
	&__body {
		position: relative;
		z-index: 3;

		display: flex;
		justify-content: space-between;
		align-items: center;

		padding: 20px;
	}

	&__logo {
		color: #fff;
	}
	&__burger {
		display: none;
		flex: 0 0 auto;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
	}
	&__item {
		&:not(:last-child) {
			margin-right: 30px;
		}
	}
	&__link {
		position: relative;

		display: inline-block;
		color: #fff;
	}

	@media (max-width: $md3+px) {
		&__burger {
			position: relative;
			z-index: 3;

			display: block;

			width: 40px;
			height: 20px;
		}
		&__burger span {
			position: absolute;
			left: 0;
			top: 9px;

			width: 100%;
			height: 2px;

			background-color: #fff;

			transition: transform 0.2s ease 0s;
		}
		&__burger:before,
		&__burger:after {
			content: "";
			position: absolute;
			left: 0;

			width: 100%;
			height: 2px;

			background-color: #fff;

			transition: all 0.2s ease 0s;
		}
		&__burger:before {
			top: 0;
		}
		&__burger:after {
			bottom: 0;
		}
		&__burger.active span {
			transform: scale(0);
		}
		&__burger.active:before {
			top: 9px;

			transform: rotate(45deg);
		}
		&__burger.active:after {
			bottom: 9px;

			transform: rotate(-45deg);
		}
		&__menu {
			position: fixed;
			top: 0;
			left: 0;

			width: 100%;
			height: 100%;

			padding: 10px;
			padding-right: 60px;

			overflow: auto;

			background-color: rgb(23, 23, 26);
			transform: translateY(-101%);
			transition: transform 0.3s ease 0s;
		}
		&__menu.active {
			transform: translateY(0%);
		}
		&__list {
			display: block;
		}
		&__item {
			margin: 0 0 20px 0px;
			&:not(:last-child) {
				margin-right: 0;
			}
		}
	}
}
