/* Normalize */
@import "_modnormalize.scss";
//  //

/* Mixins */
@import "_mixins";
//  //

/* Fonts */
@import "_fonts";
//  //

/* Variables */
@import "_variables";
//  //

/* Templates */
%reset {
	margin: 0;
	padding: 0;
	border: 0;
}

%scroll-del {
	&::-webkit-scrollbar {
		// chrome based
		width: 0px; // ширина scrollbar'a
		background: transparent; // опционально
	}
	-ms-overflow-style: none; // IE 10+
}
// @extend %scroll-del;
//  //

/* Components */
@import "_common";
@import "_settings";
@import "components/_header.scss";
// @import "components/";
@import "components/_footer.scss";
@import "components/_modals.scss";
@import "_animations";

.main-content {
	margin-top: 80px;
}
.hero__logo {
	margin-bottom: 20px;
}
.block {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 50vh;
	&--1 {
		background-color: #eb5e54;
	}

	&--2 {
		background-color: #6b75db;
	}

	&--3 {
		background-color: #bc60bf;
	}

	&--4 {
		background-color: #65e06d;
	}

	&--5 {
		background-color: #ffee54;
	}

	&--6 {
		background-color: #f27a46;
	}
}

body {
	background-image: url(data:image/png;base64,);
	background-color: #070707;
	color: #fff;
	font-family: "Montserrat", sans-serif;
}
.logo {
	position: relative;
	display: inline-block;
	color: #fff;

	font-size: 45px;
	font-weight: 700;
	text-transform: uppercase;
	text-decoration: none;

	transition: color 0.2s ease;
	&::before {
		content: "< ";
		color: #fff;
		transition: color 0.2s ease 0.1s;
	}
	&::after {
		content: " / >";
		color: #ff3e3e;
		transition: color 0.2s ease 0.1s;
	}
	&:hover {
		color: #ff3e3e;
		span {
			color: #fff;
		}
		&::after {
			color: #fff;
		}
		&::before {
			color: #ff3e3e;
		}
	}

	@media (max-width: $md3+px) {
		font-size: 36px;
	}
	@media (max-width: $md4+px) {
		font-size: 30px;
	}
}
.color-red {
	color: #ff3e3e;
	transition: color 0.2s ease;
}
