.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
  color: #000;
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 3;
}

.header__body {
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}

.header__logo {
  color: #fff;
}

.header__burger {
  display: none;
  flex: 0 0 auto;
}

.header__list {
  display: flex;
  flex-wrap: wrap;
}

.header__item:not(:last-child) {
  margin-right: 30px;
}

.header__link {
  position: relative;
  display: inline-block;
  color: #fff;
}

@media (max-width: 767.98px) {
  .header__burger {
    position: relative;
    z-index: 3;
    display: block;
    width: 40px;
    height: 20px;
  }
  .header__burger span {
    position: absolute;
    left: 0;
    top: 9px;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: transform 0.2s ease 0s;
  }
  .header__burger:before, .header__burger:after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all 0.2s ease 0s;
  }
  .header__burger:before {
    top: 0;
  }
  .header__burger:after {
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }
  .header__burger.active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }
  .header__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 10px;
    padding-right: 60px;
    overflow: auto;
    background-color: #17171a;
    transform: translateY(-101%);
    transition: transform 0.3s ease 0s;
  }
  .header__menu.active {
    transform: translateY(0%);
  }
  .header__list {
    display: block;
  }
  .header__item {
    margin: 0 0 20px 0px;
  }
  .header__item:not(:last-child) {
    margin-right: 0;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  font-size: 24px;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.7s ease, visibility 0.7s ease;
}

.modal::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal.open .modal__content {
  opacity: 1;
  transform: translate(0px, 0px);
}

.modal__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  padding: 30px;
}

.modal__content {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 30px;
  border-radius: 30px;
  background-color: #f2eef7;
  opacity: 0;
  color: black;
  transform: translate(0px, -100%);
  transition: opacity 0.7s ease, transform 0.7s ease;
}

.modal__close {
  position: absolute;
  top: 0;
  right: 15px;
  display: block;
  background: transparent;
  color: black;
  font-size: 32px;
  cursor: pointer;
  transition: transform 0.2s ease-in;
}

.modal__close:focus:not(:focus-visible) {
  outline: none;
}

@media (max-width: 767.98px) {
  .modal__close {
    outline: none;
  }
}

@media (min-width: 767.98px) {
  .modal__close:hover {
    transform: scale(1.2);
  }
  .modal__close:focus {
    transform: scale(1.2);
  }
}

